/* --- Html base parts --- */

html,
body {
	height: 100%;
	margin: 0;
	padding: 0;
	-webkit-user-select: none; /* Safari */
	-ms-user-select: none; /* IE 10 and IE 11 */
	user-select: none; /* Standard syntax */
}

body {
	background-color: #aaa;
	font-family: cursive;
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	justify-content: space-around;
	align-items: stretch;
	align-content: stretch;
	min-height: 100vh;
}

/* --- base HTML elements --- */
#header h1 {
	margin: 0;
	/* font-size: 3.5em; */
	/* background: linear-gradient(to top, red, black); */
	background: var(--sfumaturaTitolo);
	-webkit-background-clip: text;
	background-clip: text;
	-webkit-text-fill-color: transparent;
	background-clip: text;
	color: transparent;
	font-size: 7.5vh;
}

h2 {
	margin: 0;
	/* background: linear-gradient(to top, black, red); */
	background: var(--sfumaturaSottotitolo);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	background-clip: text;
	color: transparent;
	font-size: 3vh;
}
.display h1,
.display h2 {
	margin: 0.5em;
	background: linear-gradient(to top, black, white);
	-webkit-background-clip: text;
	background-clip: text;
	-webkit-text-fill-color: transparent;
}

img {
	max-width: 100%;
	max-height: 100%;
}

/* --- GameHUB base parts --- */

#header {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: space-around;
	align-items: center;
	align-content: stretch;
	background-color: #fff;
	height: 15%;
}

#body {
	display: flex;
	flex-direction: row;
	background-color: #777;
	padding: 1vh;
	height: 100%;
	z-index: 0;
}

#footer {
	display: flex;
	background-color: #fff;
	height: 20%;
}
#footerText {
	display: flex;
	flex-wrap: wrap;
	justify-content: center; /* opzionale: centra gli elementi */
	align-items: flex-start; /* opzionale */
	/* grid-template-rows: repeat(5, 1fr); */
	/* gap: 1px; */
}
#footerText p {
	flex: 0 0 48%; /* circa met√† larghezza, lascia spazio per il gap */
	box-sizing: border-box;
	margin: 0;
	padding: 1vh;
	font-size: 2vh;
	text-align: center;
}

/* --- GameHUB header parts --- */

#titleZone {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 1vh;
	border-radius: 3vh;
	flex-shrink: 1;
}

#logo,
#achievements,
#settings,
#accountLogo {
	width: 12.5vh;
	padding: 1vh;
	border-radius: 3vh;
}
#logo img,
#achievements img,
#settings img,
#accountLogo img {
	cursor: pointer;
}
#logo img,
#settings img,
#accountLogo img {
	border-radius: 50%;
}

.display {
	background: #000;
	/* background: radial-gradient(
		circle,
		rgba(0, 0, 0, 1) 60%,
		rgba(100, 0, 0, 1) 100%
	); */
	background: var(--sfumaturaSfondo);
	display: flex;
	flex-direction: column;
	text-align: center;
	align-items: center;
	background-color: #fff;
	height: 100%;
	flex: 1;
	overflow: auto;
}
.display #accountLogo {
	margin: 0 auto;
	display: block;
}
/* --- GameHUB body parts --- */

#gameZone {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
	background-color: #000;
	color: #fff;
	height: 100%;
	flex: 1;
	z-index: 1;
}

.ad {
	background-color: #f00;
	width: 10%;
	z-index: 1;
}

/* --- GameHUB footer parts --- */

/* --- Custom --- */
.hidden {
	display: none !important;
}

.coloredBox {
	display: inline-block;
	width: 1em;
	height: 1em;
	vertical-align: right;
	margin-left: 0.5em;
	border: 1px solid #aaa;
}

.btn {
	font-size: clamp(1vw, 2vw, 4vw);
	color: #000;
	padding: 1vw;
	background: #ff0;
	box-shadow: 0em 0em 0.5em 0.5em #ff0;
	border-radius: 50%;
	cursor: pointer;
	transition: 1s;
	text-align: center;
}
.btn:hover {
	color: #777;
	font-size: clamp(2vw, 3vw, 5vw);
	background: #0f0;
	box-shadow: 0em 0em 0.5em 0.5em #0f0;
}

.btn#back {
	position: absolute;
	left: 8vw;
}
.btn#next {
	position: absolute;
	right: 8vw;
}
.btn#exit {
	order: 2;
}

.game {
	max-width: 20vw;
	max-height: 20vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border: 1px solid #fff;
	padding: 4vw;
	border-radius: 50%;
	background-color: #fff;
	color: #000;
	cursor: pointer;
	order: 1;
}
.game h2 {
	font-size: 2vw;
}
.game img {
	width: 8vw;
	height: 8vw;
}

/* --- Custom Variables--- */
:root {
	--sfumaturaTitolo: linear-gradient(to top, red, black);
	--sfumaturaSottotitolo: linear-gradient(to top, black, red);
	--sfumaturaSfondo: radial-gradient(
		circle,
		rgba(0, 0, 0, 1) 60%,
		rgba(100, 0, 0, 1) 100%
	);
}

.column {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 10vh;
}
